@font-face {
	font-family: Arita-buriM;
	src: url(../../../fonts/Arita-buriM.otf);
}

$font-list: Arita-buriM, sans-serif;

html {
	background-color: #362113;
	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
}

@mixin DefaultStyle($special){
	background-color: #362113;
	border: 0.7vmin solid #83502e;
	border-radius: 1vmin;

	color: #CE8B54;
	font-family: inherit;
	font-weight: 500;
	font-size: 2.5vmin;
	letter-spacing: 0.3vmin;

	margin: 1% 0;

	height: 6vmin;

	transition: border-color 0.2s ease-in-out;

	&:focus { border: 0.7vmin solid #ce8b54; outline: none; }

	@if $special == "text" {
		padding: 0 2vmin;
		width: 90%;
	}

	@if $special == "select" {
		display: inline;
		float: left;
		width: 50%;
		margin-left: calc(5% - 3vmin);
		&option { background-color: #362113; color: #CE8B54; }
	}

	@if $special == "button" {
		display: inline;
		background-color: #362113;
		padding: 0;

		float: right;
		margin-right: calc(5% - 3vmin);

		width: 30%;
		
		transition: border-color 0.2s ease-in-out, background-color 0.2s ease-in-out;

		&:hover { background-color: #714b2c; }
	}
}

#keyPages {
	text-align: center;
	font-family: $font-list;

	h1 { font-size: 10vmin; color: yellow; filter: blur(0.06vmin); 
		letter-spacing: 1vmin; text-shadow: 0.3vmin 0.3vmin 1vmin #fcfc5e; 
		font-weight: 600
	}

	input[type=text] { @include DefaultStyle("text"); }

	select { @include DefaultStyle("select"); }

	#advancedSetting { @include DefaultStyle("button"); }

	#Note { 
		text-align: center; 
		color: gray;
		font-weight: 900;
		font-size:2.5vmin;
	}

	#keyPageList {
		margin: 1%;
		text-align: left;
	}
}

